import { createTheme } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary:{
            main: '#F27405',
        },
        secondary: {
            main: '#056CF2',
        },
    },
    typography:{
        h1: {
            fontSize: '34px',
            fontFamily: 'Segoe UI',
            fontWeight: '600',
            color: 'grey[900]',
        }
    }
});


export default theme;