import React from "react";
import { AppBar, Toolbar, Tabs, Tab, Button, useMediaQuery} from "@mui/material";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import { NavLink } from "react-router-dom";
import { ThemeProvider } from '@mui/material/styles';
import theme from './ui/Theme.js';
import DrawerComp from './Drawer.js';
import logo from "../assets/logo.svg";

function Nav() {

  const isMatch = useMediaQuery(theme.breakpoints.down('md'));

  return (
    <ThemeProvider theme={theme}>
    <AppBar position="static">
      <Toolbar>
        <img src={logo} width="80px"/>


   
    {
      isMatch ? (
        <DrawerComp />
      ):(
        <>
        <Tabs>
        <NavLink to="/"> <Tab label="Home" textColor="primary"/></NavLink>

        <NavLink to="/about"> <Tab label="About" textColor="primary"/></NavLink>

        <NavLink to="/contact"> <Tab label="Contact" textColor="primary"/></NavLink>
        </Tabs>

        <Button sx={{marginLeft: "auto" }} variant="contained">
        <AccountCircleIcon /> Log in
        </Button>
        </>
      )
    }
     </Toolbar>
    </AppBar>
    </ThemeProvider>
  );
}

export default Nav;
